import { POST, PUT } from '../../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, error } from "../../../service/MessageBox"
import moment from "moment";

export default {
  name: "input-client",
  components: { HalfCircleSpinner },
  props: ["form", "onClose", "loadData", "textTitle", "disableView", "disableEdit"],
  data() {
    return {
      isLoading: true
    };
  },
  beforeMount: function () { },
  mounted() { },
  methods: {
    onSave: function () {
      try {
        this.isLoading = true
        let dataReq = {
          Id: this.form.Id,
          TaxType: this.form.TaxType,
          IsNPWP: this.form.IsNPWP,
          StartDPP: this.form.StartDPP,
          EndDPP: this.form.EndDPP,
          Tarif: this.form.Tarif,
          ValidFrom: moment(this.form.ValidFrom).format("YYYY-MM-DD HH:mm:ss"),
          ValidTo: moment(this.form.ValidTo).format("YYYY-MM-DD HH:mm:ss")
        }
        if (dataReq.Id === null) {
          POST("tariftax", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Disimpan")
            this.onClose();
          })
        } else {
          PUT("tariftax", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Di Update")
            this.onClose();
          })
        }
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
  }
};
