import formPajak from "./formPajak";
import { POST, GET, PUT, DELETE } from '../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox"

export default {
  name: "client",
  components: { formPajak, HalfCircleSpinner },
  data() {
    return {
      isLoading: false,
      isShow: false,
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      disableView: false,
      disableEdit: false,
      textTitle: "",
      dataPajak: [],
      form: {
        Id: null,
        TaxType: null,
        IsNPWP: null,
        StartDPP: null,
        EndDPP: null,
        Tarif: null,
        ValidFrom: null,
        ValidTo: null
      },
      headers: [{
        text: "Aksi",
        value: "aksi",
        sortable: false,
        align: "left",
        class: ""
      }, {
        text: "Tax Type",
        value: "TaxType",
        align: "center"
      }, {
        text: "Is NPWP",
        value: "IsNPWP",
        align: "center"
      }, {
        text: "Start DPP",
        value: "StartDPP",
        align: "center"
      }, {
        text: "End DPP",
        value: "EndDPP",
        align: "center"
      }, {
        text: "Tarif",
        value: "Tarif",
        align: "center"
      }]
    };
  },
  created() {
    this.loadData()
  },
  beforeMount: function () { },
  computed: {},
  methods: {
    toggleInputDialog: function () {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Pajak"
    },
    onClose: function () {
      this.isShow = false;
      this.disableEdit = false;
      this.disableView = false;
      this.resetForm()
    },
    loadData: function () {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`tariftax/tariftax_cr/${page}/${rowsPerPage}`).then((res) => {
          this.dataPajak = res.Data.map(x => {
            return ({
              Id: x.Id,
              TaxType: x.TaxType,
              IsNPWP: x.IsNPWP,
              StartDPP: x.StartDPP,
              EndDPP: x.EndDPP,
              Tarif: x.Tarif,
              ValidFrom: x.ValidFrom,
              ValidTo: x.ValidTo
            })
          })
          this.totalData = res.TotalData;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onEdit: function (id) {
      try {
        this.isLoading = true
        POST('tariftax/tariftax_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = false;
          this.disableEdit = true;
          this.textTitle = "Ubah Pajak"
          this.isShow = true;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    onDelete: function (id) {
      deleteData(id, this.delData)
    },
    delData: function (id) {
      try {
        this.isLoading = true
        DELETE(`tariftax/${id}`).then((res) => {
          success("Data Berhasil Dihapus")
          this.loadData()
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onView: function (id) {
      try {
        this.isLoading = true;
        POST('tariftax/tariftax_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = true;
          this.isShow = true;
          this.textTitle = "Lihat Pajak"
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    resetForm: function () {
      this.form = {
        Id: null,
        TaxType: null,
        IsNPWP: null,
        StartDPP: null,
        EndDPP: null,
        Tarif: null,
        ValidFrom: null,
        ValidTo: null
      }
    }
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`tariftax/tariftax_cr/${page}/${rowsPerPage}`).then((res) => {
            this.dataPajak = res.Data.map(x => {
              return {
                Id: x.Id,
                TaxType: x.TaxType,
                IsNPWP: x.IsNPWP,
                StartDPP: x.StartDPP,
                EndDPP: x.EndDPP,
                Tarif: x.Tarif,
                ValidFrom: x.ValidFrom,
                ValidTo: x.ValidTo
              };
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  }
};
